/* admin_contact_page_header */

#admin_contact_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
}

#admin_contact_page_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

#admin_contact_page_header_nav_a1{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
}
#admin_contact_page_header_nav_a1:hover{
	background-color: var(--fourth-color);
	color: black;
}

#admin_contact_page_header_nav_h1{
	margin-right: 10%;
}

#admin_contact_page_header_nav_a2{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
}
#admin_contact_page_header_nav_a2:hover{
	background-color: var(--fourth-color);
	color: black;
}


/* admin_contact_page_main */

#admin_contact_page_main{
	width: 100%;
	height: 110vh;
	background-color: var(--third-color);
	color: var(--fourth-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	padding-bottom: 5%;
}

/* admin_contact_page_main_section */

#admin_contact_page_main_section{

	width: 100%;
	height: auto;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-start;
}

/* admin_contact_page_main_section_articles*/

.admin_contact_page_main_section_article{
	border: var(--fourth-color) solid 2px;
	border-radius: 5px;
	width: 80%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
	height: 15vh;
	margin-left: 3%;
	position: relative;

	margin-top: 2%;
}

.admin_contact_page_main_section_article_div{
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	align-items: center;
	width: 100%;
	min-height: 100%;
	padding-left: 3%;
	box-sizing: border-box;
	margin-bottom: 3%;

}

.admin_contact_page_main_section_article_div_h3{
	width: 75%;
	font-size: 150%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.admin_contact_page_main_section_article_div_span{
	width: 25%;
	height: 100%;
	border-left: var(--fourth-color) solid 2px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	font-size: 120%;
}
.admin_contact_page_main_section_article_div_span:hover{
	background-color: var(--fourth-color);
	color: var(--third-color);
}

.admin_contact_page_main_section_article_div1{
	background-color: rgba(192, 200, 250);
	border: solid white 4px;
	border-radius: 10px;
	color: var(--third-color);
	width: 100%;
	height: auto;
	position: absolute;
	top: 105;
	left: 0;
	z-index: 2;
	box-sizing: border-box;
}

/* admin_contact_page_main_section_article_div1 (VIEW USERS) */
#admin_contact_page_main_section_section1_article_div1{
	padding: 2%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	display: none;
	
}

#admin_contact_page_main_section_section1_article_div1_loading{
	display: none;
}

#admin_contact_page_main_section_section1_article_div1_h4{
	background-color: var(--third-color);
	color: var(--fourth-color);
	padding: 1%;
	font-size: 110%;
	display: none;
}

#admin_contact_page_main_section_section1_article_div1_h41{
	background-color: var(--third-color);
	color: var(--fourth-color);
	padding: 1%;
	font-size: 110%;	
}

#admin_contact_page_main_section_section1_article_div1_ul1{
	width: 98%;
	display: flex;
	flex-direction: row;
	align-items: center;
	flex-wrap: wrap;
	list-style: none;
}

.admin_contact_page_main_section_section1_article_div1_ul1_li{
	margin-right: 2%;
	background-color: var(--main-color);
	color: var(--fourth-color);
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
	cursor: pointer;
}
.admin_contact_page_main_section_section1_article_div1_ul1_li:hover{
	background-color: white;
	color: var(--third-color);
}
.admin_contact_page_main_section_section1_article_div1_ul1_li_selected{
	background-color: white;
	color: var(--third-color);
}

#admin_contact_page_main_section_section1_article_div1_div2{
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

#admin_contact_page_main_section_section1_article_div1_div2_h5{
	border-bottom: var(--third-color) solid 2px;

	font-size: 120%;
	padding: 1%;
}

#admin_contact_page_main_section_section1_article_div1_div2_p{
	padding: 1%;
	font-size: 110%;
	margin-left: 1%;
}



#admin_contact_page_main_section_section1_article_div1_div1{

	width: 98%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	margin-top: 2%;
	margin-bottom: 2%;
}

#admin_contact_page_main_section_section1_article_div1_div1_h5{

	background-color: var(--third-color);
	color: var(--fourth-color);
	padding: 1%;
	margin-right: 1%;
	font-size: 110%;
}

#admin_contact_page_main_section_section1_article_div1_div1_p{
	border: solid var(--third-color) 2px;
	padding: 2%;
	width: 70%;
	height: 98%;
	overflow-y: scroll;
	font-size: 105%;
}

